form {
    font-family: 'Pacifico', cursive;
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 20px;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #000000;
    margin-bottom: 26px;
  }
  
  textarea {
    font-family: 'Pacifico', cursive;
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
  }
  
  textarea::placeholder {
    color: #A5A5A5;
    font-weight: 400;
  }
  
  .textarea:focus {
    outline-color: #ff9100;
  }
  .textareaInvalidCharacter:focus{
    outline-color: #ff9898 ;
  }
 
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Pacifico', cursive;
  }
  
  .TodoForm-button--add {
    background: rgb(255, 145, 0);
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    color: white;
  }

  .TodoForm-button--add:disabled{
    opacity: 0.3;
    background-color: gray;
    cursor: not-allowed;
  }
  
  .TodoForm-button--cancel {
    background: transparent;
  }

  .TodoForm-button--cancel:hover{
    background: rgb(211, 208, 208);
    color: #fff;
  }